const validate = (data) => {
  const errors = {};

  if (!data.current.trim()) {
    errors.current = "    ";
  } else {
    delete errors.current;
  }

  if (!data.new.trim()) {
    errors.new = "     ";
  } else if (!/(?=.{8,})/.test(data.new)) {
    errors.new = "       7  ";
  } else if (!/(?=.*[a-z])/.test(data.new)) {
    errors.new = "         a-z ";
  } else if (!/(?=.*[A-Z])/.test(data.new)) {
    errors.new = "         A-Z ";
  } else if (!/(?=.*[0-9])/.test(data.new)) {
    errors.new = "        9-0 ";
  } else {
    delete errors.new;
  }

  return errors;
};

export default validate;
